@charset "UTF-8";
/*--------------------------------
 全体
---------------------------------*/
html {
  font-size: 62.5%;
}

body {
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-size: 1.4rem;
  color: #fff;
  background-color: #000;
}
@media screen and (min-width: 960px) {
  body {
    font-size: 1.5rem;
  }
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

a {
  transition: opacity 0.3s;
  text-decoration: none;
  color: #fff;
}

li {
  list-style: none;
}

.sp-only {
  display: block;
}
@media screen and (min-width: 960px) {
  .sp-only {
    display: none;
  }
}

.pc-only {
  display: none;
}
@media screen and (min-width: 960px) {
  .pc-only {
    display: block;
  }
}

/*--------------------------------
 見出し
---------------------------------*/
.heading-primary {
  font-size: 2.6rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  line-height: 1;
  text-align: center;
}
@media screen and (min-width: 960px) {
  .heading-primary {
    font-size: 3.4rem;
  }
}

/*--------------------------------
ボタン
---------------------------------*/
.btn {
  position: relative;
  display: inline-block;
  width: 160px;
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 0.01em;
  line-height: 1;
  padding: 18px 0;
  transition: all 0.4s ease-in-out;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  border-radius: 50px;
  background-size: 300% 100%;
  background-image: linear-gradient(to right, #6253e1, #852d91, #a3a1ff, #f24645);
  box-shadow: 0 4px 15px 0 rgba(126, 52, 161, 0.75);
}
@media screen and (min-width: 960px) {
  .btn {
    width: 220px;
  }
}
.btn .material-icons {
  position: absolute;
  font-size: 2.4rem;
  top: 50%;
  right: 3.5rem;
  transition: 0.3s;
  transform: translateY(-50%);
}
@media screen and (min-width: 960px) {
  .btn .material-icons {
    right: 6rem;
  }
}
@media screen and (min-width: 960px) {
  .btn:hover {
    text-decoration: none;
    transition: all 0.4s ease-in-out;
    background-position: 100% 0;
  }
  .btn:hover .material-icons {
    right: 5rem;
  }
}

/*--------------------------------
 レイアウト
---------------------------------*/
.wrapper {
  padding-top: 60px;
}
@media screen and (min-width: 960px) {
  .wrapper {
    padding-top: 80px;
  }
}

.section {
  padding: 60px 0;
}
@media screen and (min-width: 960px) {
  .section {
    padding: 90px 0;
  }
}
.section__body {
  margin-top: 20px;
}
@media screen and (min-width: 960px) {
  .section__body {
    margin-top: 40px;
  }
}

.container {
  max-width: 1340px;
  margin: 0 auto;
  padding: 0 20px;
}
@media screen and (min-width: 960px) {
  .container {
    padding: 0 40px;
  }
}

/*--------------------------------
ヘッダー
---------------------------------*/
.header {
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  height: 60px;
  z-index: 10;
  width: 100%;
  background-color: #000;
  box-shadow: 0 5px 10px -6px rgba(0, 0, 0, 0.8);
}
@media screen and (min-width: 960px) {
  .header {
    height: 80px;
    padding: 0 40px;
  }
}
.header__logo {
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  line-height: 1;
}
@media screen and (min-width: 960px) {
  .header__logo {
    font-size: 2rem;
  }
}

.gnav {
  display: none;
}
@media screen and (min-width: 960px) {
  .gnav {
    display: block;
  }
}
.gnav__list {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}
.gnav__link {
  position: relative;
  font-size: 1.3rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  line-height: 1;
  display: inline-block;
  padding: 5px 0;
  transition: all 0.3s;
}
.gnav__link::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 50%;
  width: 0;
  height: 3px;
  transform: translateX(-50%);
  transition: all 0.3s;
  background-color: #fff;
}
.gnav__link:hover::after {
  width: 100%;
}

/*--------------------------------
メインビジュアル
---------------------------------*/
.mv {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
  position: relative;
  background-color: darkblue;
}
.mv__inner {
  padding: 0 20px;
}
@media screen and (min-width: 960px) {
  .mv__inner {
    padding: 0 40px;
  }
}
.mv__title {
  font-size: 3rem;
  font-weight: 700;
  letter-spacing: 0.1em;
  line-height: 1.2;
}
@media screen and (min-width: 960px) {
  .mv__title {
    font-size: 4rem;
  }
}
.mv__subtitle {
  font-size: 1.6rem;
  font-weight: 700;
  letter-spacing: 0.08em;
  line-height: 1;
  margin-top: 5px;
}
@media screen and (min-width: 960px) {
  .mv__subtitle {
    font-size: 2rem;
  }
}
.mv__text {
  font-size: 1.4rem;
  line-height: 1.7;
  margin-top: 40px;
}
@media screen and (min-width: 960px) {
  .mv__text {
    font-size: 1.6rem;
    line-height: 1.8;
  }
}
.mv__footer {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  width: 50px;
  height: 50px;
  z-index: 2;
}
.mv__scrolltext {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  color: #fff;
  font-size: 0.7rem;
  letter-spacing: 0.05em;
}
.mv__scrolltext::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  background-color: #fff;
  animation-name: down-arrow-anime;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

@keyframes down-arrow-anime {
  0% {
    height: 0;
    transform: translate(-50%, 0);
    opacity: 0;
  }
  30% {
    height: 30px;
    opacity: 1;
  }
  100% {
    height: 0;
    transform: translate(-50%, 50px);
    opacity: 0;
  }
}
/*--------------------------------
Works
---------------------------------*/
.works__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  row-gap: 20px;
}
@media screen and (min-width: 960px) {
  .works__inner {
    padding: 60px 0;
    flex-direction: row;
    row-gap: 0;
  }
}
.works__inner::after {
  position: absolute;
  bottom: -60px;
  left: -20px;
  width: calc(40px + 100%);
  height: 85%;
  z-index: -1;
  display: block;
  content: "";
  background-color: #4e4e4e;
}
@media screen and (min-width: 960px) {
  .works__inner::after {
    top: 50%;
    bottom: auto;
    left: auto;
    right: 0;
    width: 60%;
    height: 100%;
    transform: translateY(-50%);
  }
}
.works__img-wrapper {
  width: 100%;
  /* 背景の透過を消す */
  background-color: #000;
}
@media screen and (min-width: 960px) {
  .works__img-wrapper {
    width: 60%;
  }
}
.works__desc {
  flex: 1;
  text-align: center;
}
.works__link {
  margin-top: 30px;
}

/*--------------------------------
Skill
---------------------------------*/
.skill__list {
  display: grid;
  row-gap: 35px;
}
@media screen and (min-width: 960px) {
  .skill__list {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4%;
    row-gap: 50px;
  }
}
.skill__item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  column-gap: 20px;
}
.skill__img {
  flex-basis: 60px;
  height: auto;
}
.skill__body {
  flex: 1;
}
.skill__text {
  font-size: 1.3rem;
  line-height: 1.7;
  margin-top: 5px;
}
@media screen and (min-width: 960px) {
  .skill__text {
    font-size: 1.4rem;
    line-height: 1.8;
    margin-top: 10px;
  }
}

/*--------------------------------
About
---------------------------------*/
.about__inner {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 auto;
  gap: 25px 0;
}
@media screen and (min-width: 960px) {
  .about__inner {
    flex-direction: row;
    gap: 0 30px;
    max-width: 800px;
  }
}
.about__img {
  border-radius: 50%;
}
.about__img-wrapper {
  width: 150px;
  margin: 0 auto;
  border-radius: 50%;
}
@media screen and (min-width: 960px) {
  .about__img-wrapper {
    width: auto;
    flex-basis: 20%;
  }
}
.about__text {
  flex: 1;
}
.about__text > p {
  font-size: 1.4rem;
  line-height: 1.7;
}
@media screen and (min-width: 960px) {
  .about__text > p {
    font-size: 1.5rem;
    line-height: 1.8;
  }
}
.about__text > p + p {
  margin-top: 20px;
}
@media screen and (min-width: 960px) {
  .about__text > p + p {
    margin-top: 30px;
  }
}

/*--------------------------------
Contact
---------------------------------*/
.contact {
  text-align: center;
}
.contact__lead {
  font-size: 1.4rem;
  line-height: 2.0571428571;
}
@media screen and (min-width: 960px) {
  .contact__lead {
    font-size: 1.6rem;
  }
}
.contact__list {
  margin-top: 20px;
}
@media screen and (min-width: 960px) {
  .contact__list {
    margin-top: 30px;
  }
}
.contact__item + .contact__item {
  margin-left: 10px;
}
.contact__item:last-child {
  margin-left: 0;
}

/*--------------------------------
ページトップ
---------------------------------*/
.page-top {
  font-weight: bold;
  padding: 10px 0;
  cursor: pointer;
  text-align: center;
  background-color: gray;
}
.page-top .material-icons-outlined {
  color: #fff;
  vertical-align: bottom;
}

/*--------------------------------
フッター
---------------------------------*/
.footer {
  padding: 20px 0;
}
@media screen and (min-width: 960px) {
  .footer {
    padding: 30px 0;
  }
}
.footer__copyright {
  font-size: 10px;
  text-align: center;
  color: #fff;
}

/*--------------------------------
下層：Worksページ
---------------------------------*/
.works-page__list {
  display: grid;
  column-gap: 10px;
  row-gap: 20px;
  grid-template-columns: repeat(2, 1fr);
  background-color: brown;
}
@media screen and (min-width: 960px) {
  .works-page__list {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    row-gap: 40px;
  }
}
.works-page__item {
  background-color: blue;
  padding-bottom: 10px;
}
.works-page__item:hover {
  opacity: 0.9;
}
.works-page__img {
  border: 1px solid #7d7d7d;
}
.works-page__name {
  font-size: 1.2rem;
  font-weight: 700;
  margin-top: 8px;
}
.works-page__info {
  font-size: 1rem;
  margin-top: 3px;
}
@media screen and (min-width: 960px) {
  .works-page__info {
    margin-top: 5px;
  }
}

/*--------------------------------
下層：個別Worksページ
---------------------------------*/
.article-page {
  padding: 50px 0;
}
@media screen and (min-width: 960px) {
  .article-page {
    padding: 80px 0;
  }
}
.article-page__heading {
  text-align: center;
}
.article-page__body {
  max-width: 720px;
  margin: 0 auto;
}
.article-page__img {
  margin-top: 30px;
}
.article-page__desc {
  font-size: 1.4rem;
  line-height: 1.7;
  margin-top: 20px;
}
@media screen and (min-width: 960px) {
  .article-page__desc {
    font-size: 1.5rem;
    line-height: 1.8;
    margin-top: 30px;
  }
}
.article-page__title {
  position: relative;
  font-size: 1.6rem;
  padding-left: 0.8em;
  margin-top: 30px;
}
@media screen and (min-width: 960px) {
  .article-page__title {
    font-size: 1.8rem;
    padding-left: 1em;
    margin-top: 50px;
  }
}
.article-page__title::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  content: "";
  border-radius: 3px;
  background-color: #fff;
}
.article-page__text {
  font-size: 1.4rem;
  line-height: 1.7;
  margin-top: 5px;
}
@media screen and (min-width: 960px) {
  .article-page__text {
    font-size: 1.5rem;
    line-height: 1.8;
  }
}
.article-page__text + .article-page__text {
  margin-top: 20px;
}
@media screen and (min-width: 960px) {
  .article-page__text + .article-page__text {
    margin-top: 30px;
  }
}
.article-page__home-link {
  margin-top: 80px;
  text-align: center;
}

/*--------------------------------
モバイルメニュー
---------------------------------*/
.mobile-menu {
  position: fixed;
  top: 0;
  right: -180px;
  z-index: 20;
  width: 180px;
  height: 100%;
  background-color: #fff;
  box-shadow: -10px 0 35px -20px rgba(0, 0, 0, 0.25);
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
}
@media screen and (min-width: 960px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu .logo {
  width: 130px;
}
.mobile-menu__btn {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  text-align: center;
  padding: 21px 16px;
  border: none;
  outline: none;
  background: none;
  background-color: #fff;
  cursor: pointer;
  appearance: none;
}
@media screen and (min-width: 960px) {
  .mobile-menu__btn {
    display: none;
  }
}
.mobile-menu__line {
  display: block;
  position: relative;
  left: 0;
  width: 100%;
  height: 2px;
  transition: all 0.4s;
  border-radius: 4px;
  background-color: #000;
}
.mobile-menu__line::before, .mobile-menu__line::after {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  transition: inherit;
  border-radius: 4px;
  background-color: #000;
}
.mobile-menu__line::before {
  top: -8px;
}
.mobile-menu__line::after {
  top: 8px;
}
.mobile-menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 180px;
  padding: 90px 20px 20px 50px;
  row-gap: 25px;
}
.mobile-menu__item {
  font-size: 1rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  line-height: 1;
}
.mobile-menu__link {
  text-decoration: none;
  color: #000;
}
.mobile-menu__mask {
  position: fixed;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s;
}
@media screen and (min-width: 960px) {
  .mobile-menu__mask {
    display: none;
  }
}

.menu-open .mobile-menu {
  transform: translateX(-180px);
  opacity: 1;
  visibility: visible;
}
.menu-open .mobile-menu__line {
  background-color: transparent;
}
.menu-open .mobile-menu__line::before, .menu-open .mobile-menu__line::after {
  top: 0;
  background-color: #000;
}
.menu-open .mobile-menu__line::before {
  transform: rotate(45deg);
}
.menu-open .mobile-menu__line::after {
  transform: rotate(-45deg);
}
.menu-open .mobile-menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 180px;
  padding: 90px 20px 20px 50px;
  row-gap: 25px;
}
.menu-open .mobile-menu__mask {
  width: calc(100% - 180px);
  height: 100vh;
  background: #000;
  opacity: 0.3;
  z-index: 10;
  cursor: pointer;
  visibility: visible;
}

/*--------------------------------
ワークスヒーロー
---------------------------------*/
.works {
  /* フェードモード時の共通調整 */
  /* /フェードモード時の共通調整 */
  /* .anm-startedクラス付与 */
  /* /.anm-startedクラス付与 */
}
.works .swiper-fade .swiper-slide {
  transition-property: opacity, transform !important;
  pointer-events: none;
}
.works .swiper-fade .swiper-slide-active {
  pointer-events: auto;
}
.works .slide {
  display: grid;
  place-content: center;
  row-gap: 20px;
  height: 40vh;
  text-align: center;
}
@media screen and (min-width: 960px) {
  .works .slide {
    height: 500px;
    padding: 0 8rem;
    row-gap: 40px;
  }
}
.works .slide__img-wrapper {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.works .slide__img-wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
}
.works .slide__img {
  width: 100%;
  height: 100%;
  max-width: 100%;
  object-fit: cover;
  transition: 5s linear;
  transform: translateX(-0.5%) scale(1.1);
}
.works .slide__title, .works .slide__text, .works .slide__link {
  opacity: 0;
}
.works .slide__title {
  font-size: 2.8rem;
  letter-spacing: 0.05em;
  line-height: 1;
}
@media screen and (min-width: 960px) {
  .works .slide__title {
    font-size: 6rem;
  }
}
.works .slide__text {
  font-size: 1.2rem;
  line-height: 1.3;
}
@media screen and (min-width: 960px) {
  .works .slide__text {
    font-size: 1.8rem;
  }
}
.works .slide__button {
  font-size: 1.2rem;
  font-weight: 700;
  display: block;
  color: #eccc36;
}
.works .slide__button::before {
  display: inline-block;
  width: 6rem;
  height: 1px;
  margin: -2px 1.6rem 0 0;
  content: "";
  vertical-align: middle;
  background-color: currentColor;
}
.works .fraction {
  position: absolute;
  z-index: 2;
  bottom: 5%;
  right: 5%;
  height: max-content;
  font-weight: bold;
  color: #fff;
  display: flex;
  align-items: flex-end;
}
.works .fraction__current {
  font-size: 1.8rem;
  line-height: 1;
  display: inline-block;
  overflow: hidden;
  width: 2.5rem;
}
.works .fraction__num {
  display: block;
}
.works .fraction__total {
  font-size: 1.2rem;
  line-height: 1;
  vertical-align: baseline;
}
.works .fraction__total::before {
  content: " / ";
}
.works .swiper-slide[class*=-active] .slide__img {
  transition-delay: 0s;
  transform: translateX(1.5%) scale(1);
}
.works .swiper-slide.anm-started .slide__title, .works .swiper-slide.anm-started .slide__text, .works .swiper-slide.anm-started .slide__link {
  animation: works-hero-fadeIn 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.works .swiper-slide.anm-started .slide__text {
  animation-delay: 0.2s;
}
.works .swiper-slide.anm-started .slide__link {
  animation-delay: 0.4s;
}
.works .swiper-slide.anm-finished .slide__title, .works .swiper-slide.anm-finished .slide__text, .works .swiper-slide.anm-finished .slide__link {
  animation: works-hero-fadeOut 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.works .swiper-slide.anm-finished .slide__text {
  animation-delay: 0.2s;
}
.works .swiper-slide.anm-finished .slide__link {
  animation-delay: 0.4s;
}
.works .fraction__num.anm-started {
  animation: works-hero-fraction 1s cubic-bezier(0.2, 1, 0.2, 1);
}
.works .swiper-pagination {
  position: absolute;
  z-index: 2;
  bottom: 5%;
  left: 5%;
  height: max-content;
}
.works .swiper-pagination-bullet {
  display: block;
  width: 1.6rem;
  height: 2px;
  cursor: pointer;
  transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
  background-color: rgba(255, 255, 255, 0.3);
}
.works .swiper-pagination-bullet:not(:first-child) {
  margin-top: 0.8rem;
}
.works .swiper-pagination-bullet-active {
  width: 2.8rem;
  background-color: rgb(255, 255, 255);
}
@media screen and (min-width: 960px) {
  .works .swiper-pagination-bullet-active {
    width: 5rem;
  }
}

@keyframes works-hero-fadeIn {
  0% {
    letter-spacing: -0.5em;
    transform: translateZ(-700px) translateY(500px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
