@use "../globals" as *;
/*--------------------------------
 全体
---------------------------------*/
html {
  font-size: 62.5%;
}

body {
  font-family: "Helvetica Neue",
    Arial,
    "Hiragino Kaku Gothic ProN",
    "Hiragino Sans",
    Meiryo,
    sans-serif;
  @include f_around(14, false, false, false);
  color: $cWhite;
  background-color: $cBlack;

  @include mq(lg) {
    @include f_around(15, false, false, false);
  }
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

a {
  transition: opacity .3s;
  text-decoration: none;
  color: $cWhite;
}

li {
  list-style: none;
}

.sp-only {
  display: block;
  
  @include mq() {
    display: none;
  }
}

.pc-only {
  display: none;
  
  @include mq() {
    display: block;
  }
}

/*--------------------------------
 見出し
---------------------------------*/
.heading-primary {
  @include f_around(26, 700, 50, 26);
  text-align: center;

  @include mq() {
    @include f_around(34, false, false, false);
  }
}

/*--------------------------------
ボタン
---------------------------------*/

.btn {
  position: relative;
  display: inline-block;
  width: 160px;
  @include f_around(14, 700, 10, 14);
  padding: 18px 0;
  transition: all 0.4s ease-in-out;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: $cWhite;
  border-radius: 50px;
  background-size: 300% 100%;
  background-image: linear-gradient(
    to right,
    #6253e1,
    #852d91,
    #a3a1ff,
    #f24645
    );
  box-shadow: 0 4px 15px 0 rgba(126, 52, 161, 0.75);

  @include mq() {
    width: 220px;
  }
    
  & .material-icons {
    position: absolute;
    font-size: 2.4rem;
    top: 50%;
    right: 3.5rem;
    transition: 0.3s;
    transform: translateY(-50%);

    @include mq() {
      right: 6rem;
    }
  }

  &:hover {
    @include mq() {
      text-decoration: none;
      transition: all 0.4s ease-in-out;
      background-position: 100% 0;

      & .material-icons {
        right: 5rem;
      }
    }
  }
}

/*--------------------------------
 レイアウト
---------------------------------*/
.wrapper {
  padding-top: 60px;

  @include mq() {
    padding-top: 80px;
  }
}

.section {
  padding: 60px 0;

  @include mq() {
    padding: 90px 0;
  }

  // &:nth-of-type(odd) {
  //   background-color: $cBgGray;
  // }
  
  &__body {
    margin-top: 20px;

    @include mq() {
      margin-top: 40px;
    }
  }
}

.container {
  max-width: $contentMaxWidth;
  margin: 0 auto;
  padding: 0 20px;

  @include mq() {
    padding: 0 40px;
  }
}